import "./Navbar.css"
import {NavLink} from "react-router-dom"

function Navbar(){
    return(<div className="navbar">
        <h2><NavLink to="/profile">Profile</NavLink></h2>
        <h2><NavLink to="/message">Message</NavLink></h2>
        <h2><NavLink to="/users">Users</NavLink></h2>
        <h2><NavLink to="/friends">Friends</NavLink></h2>
        </div>)
}

export default Navbar;