import "./Dialog.css"
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import React from "react"


let input_ref = React.createRef()//Получаем ссылку на input

function Dialog(props){
    let message_send = ()=>{
        props.dispatch({type: "send", data: input_ref.current.value})
    }
    return(<div className="dialog">
        <div className="dialog_c">
        <div className="authors">
            {props.state.dialogs_page.dialog_names.map((user)=>{
                return <DialogItem name={user.name} key={user.id} id={user.id}></DialogItem>
            })}
        </div>
        <div className="messages">
            {props.state.dialogs_page.messages.map((user)=>{
                return <Message message={user.message} key={user.id} id={user.id}></Message>
            })}
        </div>
        </div>
        <div className="type_m">
            <input onChange={()=>{props.dispatch({type: "write_dialog", data: input_ref.current.value})}} value={props.state.dialogs_page.new_dialogt} placeholder="Enter your message" ref={input_ref}></input>
            <button onClick={message_send}>Send</button>
            <button className="delete" onClick={()=>{props.dispatch({type: "delete_dialog", data: props.id})}}>X</button>
        </div>
    </div>)
}//Привязываем к инпуту нашу созданную ссылку input_ref(28)

export default Dialog;