import Profile from "./Profile/Profile.js";
import Dialog from "./Dialog/Dialog.js";
import {Route, Routes} from 'react-router-dom';
import  User from "./User/User.js";
import  Friends from "./Friends/Friends.js";

function Content(props){
    return(
    <div className="content">
        <Routes>
            <Route path="/profile" element={
            <Profile state={props.state} dispatch={props.dispatch}></Profile>}></Route>
            <Route path="/message" element={<Dialog dispatch={props.dispatch} state={props.state}></Dialog>}></Route>
            <Route path="/users" element={<User state={props.state}></User>}></Route>
            <Route path="/friends" element={<Friends state={props.state}></Friends> }></Route>
        </Routes>
    </div>
    )
}

export default Content;