function DialogReducer(state, action){
    if(action.type === "send"){
        let m_id = state.messages[0].id + 1
        let d_id = state.dialog_names[0].id + 1
        state.messages.unshift({message: action.data, id: m_id})
        state.dialog_names.unshift({name: "David Ben Shider", id: d_id})
    }
    if(action.type === "write_dialog"){
        state.new_dialogt = action.data
    }
    if(action.type === "delete_dialog"){
        state.dialog_names.shift()
        state.messages.shift()
    }
    return state
}

export default DialogReducer;