import Johon from "./johon.jpg"
import Elon from "./elon.jpg"
import Bill from "./bill.jpg"
import ProfileReducer from "./ProfileReducer"
import DialogReducer from "./DialogReducer"

let store = {
    _state: {
        profile_page: {
            posts: [
                {text: "Hi, world.", profileName: "Elon Mask", likes: 0, dislikes: 0, id: 3},
                {text: "I'm Elon Mask, really.", profileName: "Elon Mask", likes: 0, dislikes: 0, id: 2},
                {text: "Give me some bitcoins.", profileName: "Elon Mask", likes: 0, dislikes: 0, id: 1}
                ],
            new_postt: ""
        },
        dialogs_page: {   
            dialog_names: [
                {name: "Johon", id: "3"},
                {name: "Elon Mask", id: "2"},
                {name: "Bill Geits", id: "1"}
            ],
            messages:[
                {message: "Hello", id: "3"},
                {message: "Go to Mars", id: "2"},
                {message: "Where is my chip?", id: "1"}
            ],
            new_dialogt: ""
        },
        nav_menu: {
            users: [
            {name: "Johon", id: "3", img:Johon},
            {name: "Elon Mask", id: "2", img:Elon},
            {name: "Bill Geits", id: "1", img:Bill}
            ]}
    },
    subscribe(observer){
        this.render_data = observer
    },
    dispatch(action){
        this._state.profile_page = ProfileReducer(this._state.profile_page, action)
        this._state.dialogs_page = DialogReducer(this._state.dialogs_page, action)
        this.render_data(this._state)
        },
    render_data(){},
    get_state(){
        return this._state
    }
}

export default store;