function ProfileReducer(state, action){
    if(action.type === "add_post"){
        let new_id = state.posts[0].id + 1
        state.posts.unshift({text: action.data, profileName: "Elon Mask", likes: 0, dislikes: 0, id: new_id})
    }
    if(action.type === "like_post"){
        let post = state.posts.find((element)=>{
            if(element.id === action.data){
                return element
              }
              return false
            })
            post.likes += 1
    }
    if(action.type === "dislike_post"){
        let post = state.posts.find((element)=>{
            if(element.id === action.data){
                return element
              }
              return false
            })
            post.dislikes += 1
    }
    if(action.type === "write_post"){
        state.new_postt = action.data
    }
    if(action.type === "delete_post"){
        let id = action.data
        state.posts = state.posts.filter((element)=>{
            if(element.id === id){
                return false
            }
            else{
                return true
            }
        })
    }
    return state
}

export default ProfileReducer;