import "./Post.css";
import Em from "./elon_mini.jpg"

function Post(props){
    return(<div className="post">
        <img src={Em} alt=""></img>
        <span>{props.profileName}</span>
        <div className="post_content">
            {props.text}
            <p onClick={()=>{props.dispatch({type: "like_post", data: props.id})}} className="likes">likes: {props.likes}</p>
            <p onClick={()=>{props.dispatch({type: "dislike_post", data: props.id})}} className="dislikes">dislikes: {props.dislikes}</p>
            <button className="delete" onClick={()=>{props.dispatch({type: "delete_post", data: props.id})}}>X</button>
        </div>
    </div>)
}

export default Post;