import "./Posts.css"
import Post from "./Post/Post.js"
import React from "react"

let input_ref = React.createRef()
function Posts(props){
    let posts = props.state.profile_page.posts
    let posts_send = ()=>{
        props.dispatch({type: "add_post", data: input_ref.current.value})
     }
    return(<div className="posts">
        <h2>My posts</h2>
        <input onChange={()=>{props.dispatch({type: "write_post", data: input_ref.current.value})}} value={props.state.profile_page.new_postt} placeholder="Enter the post" ref={input_ref}></input>
        <button onClick={posts_send}>Add post</button>
        {posts.map((post)=>{
        return <Post id={post.id} key={post.id} dispatch={props.dispatch} text={post.text} profileName={post.profileName} dislikes={post.dislikes} likes={post.likes}></Post>
        })}
    </div>)               
}

export default Posts;